import { request } from 'utils';

request.prefix = '/api/categories';

export const getAreas = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    url: '/Area',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getAreaById = id =>
  request({
    url: `/Area/GetById`,
    params: { id },
    method: 'GET',
  });

export const deleteArea = id =>
  request({
    url: `/Area`,
    data: { id },
    method: 'DELETE',
  });

export const updateArea = ({ id, label }) =>
  request({
    url: `/Area`,
    data: { id, label },
    method: 'PUT',
  });

export const createArea = ({ id, label }) =>
  request({
    url: `/Area`,
    data: { id, label },
    method: 'POST',
  });



